 *{
  font-family:Helvetica,Arial,sans-serif;
  box-sizing: border-box;
  
}

body{
  padding: 60px;
  background-color: #151320;
}

input{
  color: gray;
  width: 100%;
  font-size: 1.2rem;
  border-radius:5px;
  border-color: rgb(225, 194, 251);
}

.card-grid{

  display: grid;
  grid-template-columns: repeat(auto-fill, 250px);
  grid-row: repeat(auto-fill, 250px); 

  
  }

 .card{
  
 
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  background-color: #1c1b29;
  padding: 10px;
  box-shadow: 0 0 30px (0,0,0,0.18);
 
  
} 


.card p {
  
  padding: 5px;
  text-align: center;
  color:whitesmoke;
  
 
}

.card img {
  display:block;
  max-height: 170px;
  
}

h1{
  font-size: 60px;
  color:white;
}

h3 {
  color:white;
}











